@import "variables";

@font-face {
  font-family: 'custom';
  src: url('@{icon-font-path}@{icon-font-name}.eot');
  src: url('@{icon-font-path}@{icon-font-name}.eot?#iefix') format('embedded-opentype');
}
@font-face {
  font-family: 'custom';
  src: url("data:application/x-font-ttf;charset=utf-8;base64,") format('truetype'),
  url('@{icon-font-path}@{icon-font-name}.woff') format('woff'),
  url('@{icon-font-path}@{icon-font-name}.ttf') format('truetype'),
  url('@{icon-font-path}@{icon-font-name}.svg#hellofood') format('svg');
  font-style: normal;
  font-weight: normal;
}

.custom-icon() {
  font-family: 'custom' !important;
  font-size: 1.33333333em;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  text-transform: none;

  /* Enable Ligatures ================ */
  letter-spacing: 0;
  -webkit-font-feature-settings: "liga";
  -moz-font-feature-settings: "liga=1";
  -moz-font-feature-settings: "liga";
  -ms-font-feature-settings: "liga" 1;
  -o-font-feature-settings: "liga";
  font-feature-settings: "liga";

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

// Catchall baseclass
i, [class^="icon-"], [class*=" icon-"] {
  .custom-icon();
}

.icon-stack {
  position: relative;
  display: inline-block;
  width: 2.1em;
  height: 2.1em;
  line-height: 2em;
  vertical-align: middle;
  overflow: hidden;
  clear: both;

  .front {
    line-height: inherit;
    color: @white;

    .icon-edit {
      letter-spacing: -1px;
    }
  }

  .back {
    .icon-2x();
  }

  i, [class^="icon-"], [class*=" icon-"] {
    position: absolute;
    left: 0;
    width: 100%;
    text-align: center;
  }

  &.stack-xs {
    width: 1.6em;
    height: 1.6em;
    line-height: 1.6em;

    .front {
      font-size: 1.1em;
    }

    .back {
      font-size: 1.5em;
    }
  }
}

.icon-1x { font-size: 1em; }
.icon-1-5x { font-size: 1.5em; }
.icon-2x { font-size: 2em; }
.icon-3x { font-size: 3em; }
.icon-4x { font-size: 4em; }
.icon-5x { font-size: 5em; }

.icon-rotate(@radiant) {
  -webkit-transform: rotate(unit(@radiant, deg));
  -moz-transform: rotate(unit(@radiant, deg));
  -ms-transform: rotate(unit(@radiant, deg));
  -o-transform: rotate(unit(@radiant, deg));
  transform: rotate(unit(@radiant, deg));
}

.icon-rotate-180 {
  .icon-rotate(180);
}

.icon-rotate-90 {
  .icon-rotate(90);
}

